.photo-gallery {
    display: flex;
    align-items: center;
    gap: 50px;
    .photo {
        height: 500px;
            width: 800px;
        img {
            height: 100%;
            width: 100%;
            object-fit: contain;
        }
    }

    .button {
        background: #2f2f2f;
        padding: 20px;
        border-radius: 40px;
        cursor: pointer;
    }
}